import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";

import {
  showModal,
  hideModal,
  getParameterHargaEmas,
  ShowModalParameterHargaEmas,
} from "../../../actions/datamaster_action.jsx";
import { connect } from "react-redux";
import { ToastNotification } from "../../../components/helpers/notification.jsx";
import { reset } from "redux-form";
import { putDataNoTImeOut } from "../../../config/axios.jsx";
import { ErrorHandling } from "../../../components/helpers/function.jsx";
import Tabel from "../../../components/helpers/tabel.jsx";
import HeadHargaEmas from "./HeadHargaEmas.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";

const maptostate = (state) => {
  return {
    ParameterHargaEmas: state.datamaster.getParameterHargaEmas,
    hideModal: state.datamaster.modalDialog,
  };
};

class FormhargaEmas extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      columns: [
        {
          dataField: "kode_group",
          text: "Kode Group",
          sort: true,
        },
        {
          dataField: "nama_group",
          text: "Nama Group",
        },
        {
          dataField: "harga",
          text: "Harga",
          formatter: (cell) => {
            return <span>{parseFloat(cell).toLocaleString("kr-KO")}</span>;
          },
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            let dataEdit = {
              kode_group: row.kode_group,
              harga: row.harga,
            };
             
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.formEditParameterHargaEmas(dataEdit)}
                    className="btn btn-primary mr-3"
                  >
                    {" "}
                    <i className="fa fa-edit"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    this.props.dispatch(getParameterHargaEmas());
  }
  handleSubmit(data) {
    this.setState({
      isLoading: true,
    });

    let data_post = {
      kode_group: data.kode_group,
      harga: data.harga,
    };
    putDataNoTImeOut("paraemas/edit/by-kode-group", data_post)
      .then(() => {
        this.props.dispatch(hideModal());
      })
      .then(() => {
        ToastNotification("success", "Data Berhasil Diedit");
      })
      .then(() => {
        this.props.dispatch(getParameterHargaEmas());
      })
      .then(() => {
        this.setState({ isLoading: false });
      })
      .then(() => {
        this.props.dispatch(reset("HeadHargaEmas"));
      })
      .catch((error) => {
        ErrorHandling(
          error,
          "sales/reactive/by-kode-sales/",
          data.kode_sales,
          data_post,
          this.props.dispatch,
          getParameterHargaEmas(),
          hideModal(),
          "HeadHargaEmas"
        ).then(() => {
          this.setState({ isLoading: false });
        });
      });
  }
  formEditParameterHargaEmas(dataEdit) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalParameterHargaEmas(dataEdit));
    this.setState({
      isEdit: true,
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item active">Parameter Harga Emas</li>
        </ol>
        <h1 className="page-header">Parameter Harga Emas </h1>
        <Panel>
          <PanelHeader>Parameter Harga Emas</PanelHeader>
          <br />

          {/* Parameter Pint */}
          <div className="container">
            <Tabel
              keyField="kode_group"
              exportCsv={true}
              data={this.props.ParameterHargaEmas}
              columns={this.state.columns}
              empty={this.props.ParameterHargaEmas}
              textEmpty="Parameter Harga Emas Kosong"
            />
          </div>
          <br />
          {/* End Master Sales */}
          <ModalGlobal
            size="P"
            title={
              this.state.isEdit
                ? "Edit Parameter Harga Emas"
                : "Tambah Parameter Harga Emas"
            }
            content={
              <HeadHargaEmas
                isLoading={this.state.isLoading}
                isEdit={this.state.isEdit}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            }
          />

          {/* <Modal  title={ this.state.isEdit ? 'Edit Parameter HargaEmas' : 'Tambah Parameter HargaEmas' } content={ <FormModalKategori isEdit={this.state.isEdit} /> } isOpen={ this.props.hideModal} onSubmit={ (data)=>this.handleSubmit(data) } /> */}

          {/* End Tambah Master Sales  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(FormhargaEmas);
